import ModalClose from '@/assets/icons/modal_close.svg';
export default {
  components: {
    ModalClose
  },
  props: {
    type: {
      type: String,
      default: 'modal-sm'
    },
    dialog: {
      type: String,
      default: 'modal-dialog-centered'
    },
    bodyClass: {
      type: String,
      default: 'modal-body'
    },
    closeOutside: {
      type: Boolean,
      default: false
    },
    isFullHeight: {
      type: Boolean,
      default: false
    },
    closeId: {
      type: String,
      required: false
    }
  },
  methods: {
    hasSlot(name) {
      return this.$slots[name];
    },
    close() {
      this.$emit('close');
    }
  }
};