import "core-js/modules/es.string.replace-all.js";
import { getAssetColorStyle } from '@liquality/wallet-core/dist/src/utils/asset';
import { getAssetIcon } from '@/utils/asset';
import { dpUI, formatFiatUI } from '@liquality/wallet-core/dist/src/utils/coinFormatter';
import AccountTooltip from '@/components/AccountTooltip';
import { mapState } from 'vuex';
export default {
  components: {
    AccountTooltip
  },
  data() {
    return {
      showAmountsInFiat: false
    };
  },
  props: ['asset', 'amount', 'account', 'amountFiat', 'max', 'available', 'maxFiat', 'amountError', 'maxActive'],
  computed: {
    ...mapState(['fiatRates'])
  },
  methods: {
    dpUI,
    formatFiatUI,
    getAssetColorStyle,
    getAssetIcon,
    toggleShowAmountsFiat() {
      this.showAmountsInFiat = !this.showAmountsInFiat;
    },
    amountFiatInputFormat() {
      // in case of undefined/NaN value for amount in fiat, present the placeholder value
      return !this.amountFiat ? '' : this.amountFiat.replaceAll(',', '');
    }
  }
};